body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.keyboard {
    width: 340px;
    position: absolute;
    bottom: 10px;
    left: 50%;
    -webkit-transform: translateX(-50%);
    transform: translateX(-50%);
    background-color: gray;
    padding: 10px;
    border: 1px solid black;
    z-index: 1;
    display: none;
}

.letters {
    width: 33px;
    height: 33px;
    background-color: green;
    padding: 5px;
    border: 2px solid yellow;
    margin: 1px;
}
.header {
    width: 100%;
    height: 80px;
    background-color: yellow;
    border-bottom: 2px solid green;
    text-align: center;
}

.title {
    font-family: 'Indie Flower', cursive;
    font-size: 32px;
    height: 0px;
    margin-top: 0px;
}

.win {
    font-weight: bolder;
    font-size: 22px;
    text-align: center;
    margin-top: 50px;
}
* {
    box-sizing: border-box;
}

.cowpic {
    display: none;
    width: 100vw;
    height: 50vh;
}

.endgame {
    z-index: 4;
    position: absolute;
}

.loseresults {
    width: 100vw;
    height: 50vh;
    border: 4px solid black;
    background-color: red;
    display: none;
    text-align: center;
}

.winresults {
    width: 100vw;
    height: 50vh;
    border: 4px solid black;
    background-color: green;
    display: none;
    text-align: center;
}

.playagain {
    width: 80%;
    height: 60px;
    background-color: green;
    border: 2px solid black;
    font-size: 25px;
    font-weight: bolder;
}

.losstag {
    font-size: 25px;
    font-weight: bolder;
}


@media only screen and (min-width: 600px) {
    .loseresults {
        height: 20vh;
    }
    .cowpic {
        height: 80vh
    }
    .losstag {
        margin-top: 0;
    }
    .playagain {
        width: 40%;
    }
}
.App {
    height: 100vh;
    background-image: url(/CowtasticGuessingGame/static/media/cowspots.4e090f7a.jpg);
    background-repeat: no-repeat;
    background-size: cover;
}

.intro {
    font-family: 'Indie Flower', cursive;
    text-align: center;
    font-size: 20vw;
    color: pink;
    margin: 30px;
    -webkit-animation-name: bigger;
            animation-name: bigger;
    -webkit-animation-duration: 2s;
            animation-duration: 2s;
    -webkit-animation-timing-function: linear;
            animation-timing-function: linear;
    text-shadow: -2px 0 green, 0 2px green, 2px 0 green, 0 -2px green;
    font-weight: 900;
}

.startbtn {
    width: 315px;
    height: 60px;
    color: green;
    background-color: pink;
    border: 2px solid green;
    font-weight: bolder;
    font-size: 22px;
    position: absolute;
    bottom: 70px;
    left: 50%;
    -webkit-transform: translateX(-50%);
    transform: translateX(-50%);
    z-index: 1;
}

.guessdiv {
    width: 90%;
    position: absolute;
    bottom: 150px;
    left: 50%;
    -webkit-transform: translateX(-50%);
    transform: translateX(-50%);
    padding: 10px;
    z-index: 1;
    text-align: center;
    display: none;
}

.wrongdiv {
    background-color: red;
    border: 2px solid yellow;
    height: 90px;
}

.wrong {
    font-size: 22px;
    font-weight: bolder;
}

.gtxt {
    margin-top: 0px;
    font-weight: bolder;
}

.wordbox {
    width: 90%;
    position: absolute;
    bottom: 325px;
    left: 50%;
    -webkit-transform: translateX(-50%);
    transform: translateX(-50%);
    padding: 10px;
    z-index: 1;
    text-align: center;
    display: none;
    background-color: pink;
    height: 150px;
    border: 2px solid blue;
}

.word {
    font-size: 42px;
}
@media only screen and (min-width: 600px) {
    .intro {
        font-size: 100px;
        width: 50%;
        margin: 30px auto;
        -webkit-animation-name: biggerdesk;
                animation-name: biggerdesk;
    }
    .big {
        width: 50%;
    }
}

@-webkit-keyframes bigger {
    from {font-size: 0px;}
    to {font-size: 20vw;}
}

@keyframes bigger {
    from {font-size: 0px;}
    to {font-size: 20vw;}
}

@-webkit-keyframes biggerdesk {
    from {font-size: 0px;}
    to {font-size: 100px;}
}

@keyframes biggerdesk {
    from {font-size: 0px;}
    to {font-size: 100px;}
}

@-webkit-keyframes biggerdesk2 {
    from {font-size: 0px;}
    to {font-size: 30px;}
}

@keyframes biggerdesk2 {
    from {font-size: 0px;}
    to {font-size: 30px;}
}

